header {
    display: grid;
    grid-template-columns: 2fr 1.2fr;
    background-color: black;
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
}

nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 15px;
    justify-items: end;
    align-items: center;
}

.logo {
    color: white;
    text-decoration: none;
}

.logo:hover {
    color: green;
}

.nav_item {
    background-color: black;
    color: #001AFF;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    margin-right: 10px;
}

.nav_item:hover {
    color: white;
}

a {
    text-decoration: none;
}

.cart_btn {
    display: grid;
    grid-template-columns: 2fr 0.5fr;
}

.count_cart_items {
    font-size: 14px;
    font-weight: bold;
    background: rgb(15, 214, 91);
    padding: 5px;
    border-radius: 20px;
    color: #001AFF;
    margin: auto;
}

.izbrannoe_btn {
    display: grid;
    grid-template-columns: 2fr 0.5fr;
}

.count_izbrannoe_items {
    font-size: 14px;
    font-weight: bold;
    background: rgb(48, 214, 15);
    padding: 5px;
    border-radius: 20px;
    color: #001AFF;
    margin: auto;
}


